.image{
    background-image: url("https://images.pexels.com/photos/305070/pexels-photo-305070.jpeg?auto=compress&cs=tinysrgb&w=600");
    height: 638px;
    background-attachment: fixed;
    background-size: cover;
}
.btn{
    height: 200px;
    width: 400px;
    background-color: white;
    opacity: 0.8;
    box-shadow: 10px 10px 10px gray ;
    position: absolute;
    left: 36%;
    top: 34%;
    border-radius: 10px;
}
button{
    margin-top: 20px;
    margin-left: 170px;
    border-radius: 4px;
    transition: all 0.2s linear;
}
button:hover{
    transform: translateY(1px);
}
p{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}
